/* Unified stats grid */
.stats-grid{
  margin-bottom: 2rem;
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  gap: 1.5rem;
}
@media (min-width: 768px){
  .stats-grid{
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
@media (min-width: 1024px){
  .stats-grid{
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
}
